body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bg-nav-custom {
  background-color: rgb(235, 235, 235);
  opacity: 0.95;
}

/* for title subtext */
small.vsmall {
  font-size: 1rem;
}

/* card hovering functionality and other small fixes*/
.card-highlights {
  border-radius: 0;
  transition: all 0.2s;
  box-shadow: 0 0.5rem 1rem rgba(0,0,0,0.15);
  font-size: 0.75rem;
}

@media (min-width: 992px) {
  .card-highlights:hover {
    margin-top: -.25rem;
    margin-bottom: -.25rem;
    box-shadow: 0 0.5rem 1rem 0 rgba(0,0,0,0.3);
  }
}

.card-img-top {
  width: 100%;
  height: 15vw;
  object-fit: cover;
}

a.card-title-link {
  color: #000;
}

a.card-title-link:hover {
  text-decoration: underline;
  color: inherit;
}

/* Read more/less functionality */
a .readMore, .collapsed .readLess {
  display: none;
}

a .readLess, .collapsed .readMore {
  display: inline; 
}

.descriptionText .collapse {
  height: 4.5em;
  position: relative;
  display: block !important;
  overflow: hidden;
}

.descriptionText .collapsing {
  height: 4.5em
}

.descriptionText .collapse.show {
  height: auto;
}

/*modifications to legend button states */
.legend-button-inactive {
  opacity: 0.5;
  cursor: pointer;
}

.legend-button-inactive:hover {
  opacity: 1;
}

.legend-button-active {
  border: 2px solid;
  border-color: #000;
  cursor: pointer;
}

/* Table mods */
.table-filters {
  border-collapse: separate;
  border-spacing: 0 0.5em;
}

.table-filters th {
  background-color: rgb(233, 236, 239);
  font-weight: normal;
  border-radius: 0.25rem 0 0 0.25rem;
}

.table-filters td {
  border-radius: 0 0.25rem 0.25rem 0;
}

/*airTable embeds for static pages */
.airTable-embed-view {
  background: transparent;
  border: 1px solid #ccc;
  height: 533px;
  width: 100%;
}

.airTable-form-view {
  width: 100%;
  height: 3217px;
  background: transparent;
  border: 1px solid rgb(230, 230, 230);
}


/* Fading classes */
.fader {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 1.2s ease-out;
  will-change: opacity, visibility;
}
.fader.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}

/* Coloring for badges and cards...class names are derived dynamically from airtable category names*/
